@import "../../global.scss";

.about {
  background-color: #a2b29f;
  display: flex;
  align-items: center;
  justify-content: center;

  // pågående pil i botten av var del
  // .arrow-bottom1 {
  //   position: absolute;
  //   align-items: center;
  //   bottom: 10px;
  //   width: 30px;
  //   animation: arrowBlink 2s infinite;

  //   @keyframes arrowBlink {
  //     100% {
  //       opacity: 0; //animering/loop av pil, efter 2 sekunder blir opasiteten 0
  //     }
  //   }
  // }

  .box {
    height: 500px;
    display: flex;
    position: absolute;
    left: 0;

    @include mobile {
      height: 620px;
    }

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 700px;
        height: 100%;
        background-color: whitesmoke;
        // color: #66806a;
        color: #064635;
        display: flex;
        align-items: center;
        justify-content: center;

        .left {
          flex: 12;
          height: 80%;
          padding-left: 20px;
          padding-right: 20px;
          display: flex;
          align-items: center;
          justify-content: center;
          text-align: center;

          .leftContainer {
            font-size: 18px;
            font-weight: 300;
            line-height: 25px;
            letter-spacing: 1px;
            padding: 20px;

            @include mobile {
              font-size: 15px;
            }

            .title {
              font-weight: 400;
              margin-bottom: 5px;
            }
          }
        }
        .right {
          flex: 0;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .rightContainer {
            font-size: 18px;
            font-weight: 300;

            .title {
              font-weight: 400;
              margin-bottom: 5px;
            }
          }
        }
      }
    }
  }
}
