  .box {
    height: 500px;
    display: flex;
    position: absolute;
    left: 0;

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 700px;
        height: 100%;
        background-color: #333;
        color: whitesmoke;
        display: flex;
        align-items: center;
        justify-content: center;

        .left {
          flex: 6;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            font-size: 18px;
            font-weight: 300;
            line-height: 25px;
            letter-spacing: 1px;

            @include mobile {
              font-size: 16px;
            }

            .title {
              font-weight: 400;
              margin-bottom: 5px;
            }
          }
        }
        .right {
          flex: 6;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .rightContainer {
            font-size: 18px;
            font-weight: 300;
            line-height: 25px;
            letter-spacing: 1px;

            @include mobile {
              font-size: 16px;
            }

            .title {
              font-weight: 400;
              margin-bottom: 5px;
            }
          }
        }
      }
    }
  }
}
